import React, { FC, HTMLAttributes, useState, useEffect } from 'react';
import { Dialog, DialogTitle, DialogContent, DialogContentText, TextField, DialogActions, Button } from '@mui/material';
import { TActivityFeatures } from './ActivityFeatures';
import { Editor } from '../../../shared/Editor/Editor';

interface CreateOrEditModalProps extends HTMLAttributes<HTMLDivElement> {
    onSave: (title: string, shortTitle: string, popup: string, sorting: number, percent: number) => void;
    item: TActivityFeatures | Omit<TActivityFeatures, 'id'>;
    isModal: boolean;
    onClose: () => void;
}

export const CreateOrEditModal: FC<CreateOrEditModalProps> = ({ onSave, isModal, item, onClose }) => {
    // TODO: Переделать на useReducer
    const [title, setTitle] = useState('');
    const [shortTitle, setShortTitle] = useState('');
    const [editorValue, setEditorValue] = useState('');
    const [sorting, setSorting] = useState(10);
    const [percent, setPercent] = useState(0);
    const [titleError, setTitleError] = useState(false);
    const [sortingError, setSortingError] = useState(false);
    const [percentError, setPercentError] = useState(false);

    useEffect(() => {
        if(item) {
            setTitle(item.title)
            setShortTitle(item.shortTitle)
            setEditorValue(item.popup)
            setSorting(item.sorting)
            setPercent(item.percent)
        }
    }, [item])

    const handleSave = () => {
        if(title.length > 0  && sorting && percent > 0) {
            onSave(title, shortTitle, editorValue, sorting, percent)
            setTitleError(false)
            setSortingError(false)
            setPercentError(false)
        } else {
            title.length === 0 ? setTitleError(true) : setTitleError(false);
            sorting < 1 ? setSortingError(true) : setSortingError(false);
            percent < 1 ? setPercentError(true) : setPercentError(false);
        }
    }

    return (
      <Dialog open={isModal} onClose={onClose}>
            <DialogTitle>{item?.hasOwnProperty('id') ? 'Редактировать' : 'Создать'} Элемент</DialogTitle>
            <DialogContent  sx={{minWidth: '400px'}}>
            <DialogContentText>
                    Введите Надбавку:
                </DialogContentText>
                <TextField 
                    label="Особенность Деятельности"
                    variant="outlined"
                    required
                    fullWidth
                    margin="normal"
                    size="small"
                    type="text"
                    value={title}
                    onChange={(e) => setTitle(e.target.value)}
                    error={titleError}
                ></TextField>
                <TextField 
                    label="Сокращенное название"
                    variant="outlined"
                    fullWidth
                    margin="normal"
                    size="small"
                    type="text"
                    value={shortTitle}
                    onChange={(e) => setShortTitle(e.target.value)}
                ></TextField>
                <TextField 
                    label="Процент"
                    variant="outlined"
                    required
                    fullWidth
                    margin="normal"
                    size="small"
                    type="number"
                    value={percent}
                    onChange={(e) => setPercent(+e.target.value)}
                    error={percentError}
                ></TextField>
                <TextField 
                    label="Сортировка"
                    variant="outlined"
                    required
                    fullWidth
                    margin="normal"
                    size="small"
                    type="number"
                    value={sorting}
                    onChange={(e) => setSorting(+e.target.value)}
                    error={sortingError}
                ></TextField>
                <Editor placeholder='Подсказка' editorValue={editorValue} setEditorValue={setEditorValue} error={false} />
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose}>Отмена</Button>
                <Button onClick={handleSave}>Сохранить</Button>
            </DialogActions>
        </Dialog>
    )
  }