import React, { useEffect, useState } from "react";
import "./ActivityFeatures.scss";
import {
  Alert,
  Box,
  Button,
  Divider,
  Stack,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
} from "@mui/material";
import { ListItemControls } from "../../../shared/ListItemControls/ListItemControls";
import { DeleteModal } from "../../../shared/DeleteModal/DeleteModal";
import { EmptyTable } from "../../../shared/EmptyTable/EmptyTable";
import { createDocument, deleteDocument, getDocuments, updateDocument } from "../../../../core/api";
import { CreateOrEditModal } from "./CreateOrEditModal";

export type TActivityFeatures = {title: string, shortTitle: string, percent: number, id: string, sorting: number, popup: string}
export const ACTIVITY_FEATURES = 'activity-features'; // название коллекции в firebase

export const ActivityFeatures = () => {
  const [items, setItems] = useState<TActivityFeatures[]>([]) // элемнты коллекции
  const [isCreateModal, setCreateModal] = useState(false); // флаг модального окна создания элемента
  const [isEditModal, setEditModal] = useState(false); // флаг модального окна редактирования элемента
  const [isDeleteModal, setDeleteModal] = useState(false);  // флаг модального окна удаления элемента
  const [currentItem, setCurrentItem] = useState<TActivityFeatures | null>(null); // элемент выбранный для редактирования или удаления
  const sortItemsAndSetItems = (arr: TActivityFeatures[]) => setItems(arr.sort((a,b) => (a.sorting - b.sorting)));

  const onCreate = (title: string, shortTitle: string, popup: string, sorting: number, percent: number) => {
    createDocument(ACTIVITY_FEATURES, {title, shortTitle, sorting, percent, popup})
      .then(() => getDocuments(ACTIVITY_FEATURES))
      .then((response) => sortItemsAndSetItems([...response] as TActivityFeatures[]))
      .catch((error) => console.log(error))
      .finally(() => setCreateModal(false));
  };

  const onEdit = (title: string, shortTitle: string, popup: string, sorting: number, percent: number) => {
    updateDocument(ACTIVITY_FEATURES, {...currentItem, title, shortTitle, sorting, percent, popup})
      .then(() => getDocuments(ACTIVITY_FEATURES))
      .then((response) => sortItemsAndSetItems([...response] as TActivityFeatures[]))
      .catch((error) => console.log(error))
      .finally(() => {setEditModal(false), setCurrentItem(null)});
  };

  const onDelete = () => {
    deleteDocument(ACTIVITY_FEATURES, currentItem)
      .then(() => getDocuments(ACTIVITY_FEATURES))
      .then((response) => sortItemsAndSetItems([...response] as TActivityFeatures[]))
      .catch((error) => console.log(error))
      .finally(() => {setDeleteModal(false), setCurrentItem(null)})
  };

  useEffect(() => {
    getDocuments(ACTIVITY_FEATURES)
    .then((response) => sortItemsAndSetItems([...response] as TActivityFeatures[]))
    .catch((error) => console.log(error))
  }, []);

  const TableOfItems = () => (
    <TableContainer>
      <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
        <TableHead>
          <TableRow>
            <TableCell>Особенности Деятельности</TableCell>
            <TableCell>Сокращенное название</TableCell>
            <TableCell align="center">Процент, %</TableCell>
            <TableCell align="center">Сортировка</TableCell>
            <TableCell align="right">Действия</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {items.map((item) => (
            <TableRow>
              <TableCell>{item.title}</TableCell>
              <TableCell>{item.shortTitle}</TableCell>
              <TableCell align="center">{item.percent}</TableCell>
              <TableCell align="center">{item.sorting}</TableCell>
              <TableCell align="right">
                <ListItemControls
                  onDelete={() => {setCurrentItem(item), setDeleteModal(true)}}
                  onEdit={() => {setCurrentItem(item), setEditModal(true)}}
                  style={{ justifyContent: "flex-end" }}
                />
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )

  return (
      <Box my={4}>
        <Stack
          justifyContent="space-between"
          direction="row"
          alignItems="center"
          mb={2}
          px={1}
        >
          <Typography variant="h4">Особенности Деятельности</Typography>
          <Button variant="contained" disableElevation onClick={() => setCreateModal(true)}>
            Создать Надбавку
          </Button>
        </Stack>

        <CreateOrEditModal 
          isModal={isCreateModal} 
          onClose={() => setCreateModal(false)}
          item={{title: '', shortTitle: '', popup: '', sorting: 10, percent: 0}}
          onSave={onCreate}
        />

        <CreateOrEditModal 
          isModal={isEditModal} 
          onClose={() => setEditModal(false)}
          item={currentItem}
          onSave={onEdit}
        />

        <DeleteModal open={isDeleteModal} onClose={() => {setDeleteModal(false), setCurrentItem(null)}} onDelete={onDelete} item={currentItem?.id}>
          Вы собираетесь удалить Особенности Деятельности "{currentItem?.title}" ?
        </DeleteModal>

        {/* <Box my={2}>
          <Alert severity="info">
            Изменения в этой таблице могут повлиять на уже заполненные 
            прайсы!
          </Alert>
        </Box> */}

        <Divider />

        {items.length > 0 ? <TableOfItems /> : <EmptyTable>Не создано ни одного элемента.</EmptyTable>}
        </Box>
  );
};
