import { IconButton, Stack } from "@mui/material";
import React, { FC, HTMLAttributes } from "react";
import { Delete, Edit } from "@mui/icons-material";

interface ListItemControlsProps extends HTMLAttributes<HTMLDivElement> {
  onEdit: () => void;
  onDelete?: () => void;
}

export const ListItemControls: FC<ListItemControlsProps> = ({
  onDelete,
  onEdit,
  style,
}) => {
  return (
    <Stack spacing={1} direction="row" justifyContent="center" sx={style}>
      <IconButton edge="end" onClick={onEdit}>
        <Edit />
      </IconButton>
      {onDelete && <IconButton edge="end" onClick={onDelete}>
        <Delete />
      </IconButton>}
    </Stack>
  );
};
