import React, {useReducer, Reducer, FormEvent, ChangeEvent, useState} from "react";
import { Avatar, Button, CssBaseline, TextField, FormControlLabel, Checkbox, Link, Grid, Box, Typography, Container } from "@mui/material";
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { validateEmail } from "../../../core/utils";
import "./LoginPage.scss";
import { useAuth } from "../PrivateRoute/AuthProvider";
import { useLocation, useNavigate } from "react-router-dom";

type TLoginField = {
  name: string;
  error?: boolean;
  helper?: string;
  value: string;
  onChange: (e: ChangeEvent<HTMLInputElement>) => void;
};

type TLoginFormFieldState = Omit<TLoginField, 'onChange'>;

type Action = { type: 'change' | 'error'; value: string };

function Copyright(props: any) {
  return (
    <Typography
      variant="body2"
      color="text.secondary"
      align="center"
      {...props}
    >
      {"Copyright © "}
      <Link color="inherit" href="/">
        БухЭталонЪ
      </Link>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

function reducer(state: TLoginFormFieldState, action: Action): TLoginFormFieldState {
  switch (action.type) {
    case 'change':
      return {
        ...state,
        error: false,
        helper: '',
        value: action.value,
      };
    case 'error':
      return {
        ...state,
        error: true,
        helper: action.value,
      };
    default:
      throw new Error();
  }
}

// TODO remove, this demo shouldn't need to reset the theme.
const defaultTheme = createTheme();

export default function LoginPage() {
  const navigate = useNavigate();
  const location = useLocation();
  const {logIn} = useAuth();
  const [authError, setAuthError] = useState('');
  const [emailState, dispatchEmail] = useReducer<Reducer<TLoginFormFieldState, Action>>(reducer, {
    name: 'email',
    value: '',
  });

  const [passwordState, dispatchPassword] = useReducer<Reducer<TLoginFormFieldState, Action>>(reducer, {
    name: 'password',
    value: '',
  }); 
  const [isChecked, setChecked] = useState(false);

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    let valid = true;
    if (!validateEmail(emailState.value)) {
      dispatchEmail({
        type: 'error',
        value: 'Введите корректный email',
      });
      valid = false;
    }

    if (passwordState.value.length < 6) {
      dispatchPassword({
        type: 'error',
        value: 'Длинна пароля меньше 6-ти символов',
      });
      valid = false;
    }

    logIn(emailState.value, passwordState.value, isChecked)
      .then(() => navigate(location.state?.from || 'home'))
      .catch((error) => setAuthError(error?.message || 'error'))
  };
console.log(isChecked)
  return (
    <ThemeProvider theme={defaultTheme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Войти
          </Typography>
          {authError && (
            <Typography variant="subtitle2" color="error" sx={{ m: 2 }}>
              {authError}
            </Typography>
          )}
          <Box
            component="form"
            onSubmit={handleSubmit}
            noValidate
            sx={{ mt: 1 }}
          >
            <TextField
              margin="normal"
              required
              fullWidth
              variant="outlined"
              label={emailState.name}
              name={emailState.name}
              value={emailState.value}
              onChange={(e) => dispatchEmail({ type: 'change', value: e.target.value })}
              error={!!emailState.error}
              helperText={emailState.helper}
              autoFocus
            />
            <TextField
              margin="normal"
              required
              fullWidth
              type="password"
              variant="outlined"
              label={passwordState.name}
              name={passwordState.name}
              value={passwordState.value}
              onChange={(e) => dispatchPassword({ type: 'change', value: e.target.value })}
              error={!!passwordState.error}
              helperText={passwordState.helper}
              autoComplete="current-password"
            />
            <FormControlLabel
              control={<Checkbox value="remember" color="primary" onChange={(e) => setChecked(e.target.checked)} />}
              label="Запомнить меня"
              checked={isChecked}
            />
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Войти
            </Button>
            <Grid container>
              <Grid item xs>
                <Link href="#" variant="body2">
                  Забыли пароль?
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>
        <Copyright sx={{ mt: 8, mb: 4 }} />
      </Container>
    </ThemeProvider>
  );
}
