import React, { FC, HTMLAttributes, useState, useEffect } from 'react';
import { Dialog, DialogTitle, DialogContent, DialogContentText, TextField, FormControlLabel, Switch, DialogActions, Button } from '@mui/material';
import { TLegalForm } from './LegalForm';

interface CreateOrEditModalProps extends HTMLAttributes<HTMLDivElement> {
    onSave: (title: string, isShown: boolean, sorting: number) => void;
    item: TLegalForm | Omit<TLegalForm, 'id'>;
    isModal: boolean;
    onClose: () => void;
}

export const CreateOrEditModal: FC<CreateOrEditModalProps> = ({ onSave, isModal, item, onClose }) => {
    // TODO: Переделать на useReducer
    const [title, setTitle] = useState('');
    const [sorting, setSorting] = useState(10);
    const [isShown, setShown] = useState(true);
    const [titleError, setTitleError] = useState(false);
    const [sortingError, setSortingError] = useState(false);

    useEffect(() => {
        if(item) {
            setTitle(item.title)
            setShown(item.isShown)
            setSorting(item.sorting)
        }
    }, [item])

    const handleSave = () => {
        if(title.length > 0  && sorting) {
            onSave(title, isShown, sorting)
            setTitleError(false)
            setSortingError(false)
        } else {
            title.length === 0 ? setTitleError(true) : setTitleError(false);
            sorting < 1 ? setSortingError(true) : setSortingError(false);
        }
    }

    return (
      <Dialog open={isModal} onClose={onClose}>
            <DialogTitle>{item?.hasOwnProperty('id') ? 'Редактировать' : 'Создать'} Элемент</DialogTitle>
            <DialogContent  sx={{minWidth: '400px'}}>
                <DialogContentText>
                    Введите Правовую Форму:
                </DialogContentText>
                <TextField 
                    label="Правовая Форма"
                    variant="outlined"
                    required
                    fullWidth
                    margin="normal"
                    size="small"
                    type="text"
                    value={title}
                    onChange={(e) => setTitle(e.target.value)}
                    error={titleError}
                ></TextField>
                <TextField 
                    label="Сортировка"
                    variant="outlined"
                    required
                    fullWidth
                    margin="normal"
                    size="small"
                    type="number"
                    value={sorting}
                    onChange={(e) => setSorting(+e.target.value)}
                    error={sortingError}
                ></TextField>
                <FormControlLabel 
                  control={<Switch defaultChecked={isShown ? true : false } onChange={(e) => setShown(e.target.checked)} />} 
                  label="Отображать в публичной части" 
                />
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose}>Отмена</Button>
                <Button onClick={handleSave}>Сохранить</Button>
            </DialogActions>
        </Dialog>
    )
  }