import React, { useState, useEffect } from "react";
import "./News.scss";
import {
  Box,
  Button,
  Divider,
  Pagination,
  Stack,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
} from "@mui/material";
import { ListItemControls } from "../../shared/ListItemControls/ListItemControls";
import { useNavigate } from "react-router-dom";
import { deleteDocument, getDocuments } from "../../../core/api";
import { EmptyTable } from "../../shared/EmptyTable/EmptyTable";
import { DeleteModal } from "../../shared/DeleteModal/DeleteModal";

export type TNews = {title: string, link: string, date: string, id: string, announce: string, editorValue: string}
export const NEWS = 'news'; // название коллекции в firebase

export const News = () => {
  const navigate = useNavigate();
  const [items, setItems] = useState<TNews[]>([]) // элемнты коллекции
  const [isDeleteModal, setDeleteModal] = useState(false);  // флаг модального окна удаления элемента
  const [currentItem, setCurrentItem] = useState<TNews | null>(null); // элемент выбранный для редактирования или удаления
  const sortItemsAndSetItems = (arr: TNews[]) => setItems(arr.sort((a,b) => (a.date > b.date ? 1 : -1)));

  useEffect(() => {
    getDocuments(NEWS)
    .then((response) => setItems([...response] as TNews[]))
    .catch((error) => console.log(error))
  }, []);

  const onDelete = () => {
    deleteDocument(NEWS, currentItem)
      .then(() => getDocuments(NEWS))
      .then((response) => sortItemsAndSetItems([...response] as TNews[]))
      .catch((error) => console.log(error))
      .finally(() => {setDeleteModal(false), setCurrentItem(null)})
  };

  const TableOfItems = () => (
    <TableContainer>
      <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
        <TableHead>
          <TableRow>
            <TableCell>Дата</TableCell>
            <TableCell>Заголовок</TableCell>
            <TableCell align="right">Действия</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {items.map((item) => (
            <TableRow>
              <TableCell>{item.date}</TableCell>
              <TableCell>{item.title}</TableCell>
              <TableCell align="right">
                <ListItemControls
                  onDelete={() => {setCurrentItem(item), setDeleteModal(true)}}
                  onEdit={() => {setCurrentItem(item), navigate('edit/' + item.id)}}
                  style={{ justifyContent: "flex-end" }}
                />
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )

  return (
    <Box my={5}>
      <Stack
        justifyContent="space-between"
        direction="row"
        alignItems="center"
        mb={2}
        px={1}
      >
        <Typography variant="h3">Новости</Typography>
        <Button variant="contained" disableElevation onClick={() => navigate('create')}>Создать Новость</Button>
      </Stack>

      <DeleteModal open={isDeleteModal} onClose={() => {setDeleteModal(false), setCurrentItem(null)}} onDelete={onDelete} item={currentItem?.id}>
          Вы собираетесь удалить новость "{currentItem?.title}" ?
        </DeleteModal>

      <Divider />

        {items.length > 0 ? <TableOfItems /> : <EmptyTable>Не создано ни одного элемента.</EmptyTable>}
      <Divider />

      <Stack direction="row" spacing={2} mt={2}>
        <Pagination count={10} />
      </Stack>
    </Box>
  );
};
