import React, { useEffect, useState } from "react";
import "./LegalForm.scss";
import {
  Alert,
  Box,
  Button,
  Divider,
  Stack,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
} from "@mui/material";
import { ListItemControls } from "../../../shared/ListItemControls/ListItemControls";
import { DeleteModal } from "../../../shared/DeleteModal/DeleteModal";
import { EmptyTable } from "../../../shared/EmptyTable/EmptyTable";
import { createDocument, deleteDocument, getDocuments, updateDocument } from "../../../../core/api";
import { CreateOrEditModal } from "./CreateOrEditModal";

export type TLegalForm = {title: string, id: string, isShown: boolean, sorting: number}
export const LEGAL_FORM = 'legal-form'; // название коллекции в firebase

/**
 * Компонент отрисует таблицу правовой формы
 */
export const LegalForm = () => {
  const [items, setItems] = useState<TLegalForm[]>([]) // элемнты коллекции
  const [isCreateModal, setCreateModal] = useState(false); // флаг модального окна создания элемента
  const [isEditModal, setEditModal] = useState(false); // флаг модального окна редактирования элемента
  const [isDeleteModal, setDeleteModal] = useState(false);  // флаг модального окна удаления элемента
  const [currentItem, setCurrentItem] = useState<TLegalForm| null>(null); // элемент выбранный для редактирования или удаления
  const sortItemsAndSetItems = (arr: TLegalForm[]) => setItems(arr.sort((a,b) => (a.sorting - b.sorting)));

  const onCreate = (title: string, isShown: boolean, sorting: number) => {
    createDocument(LEGAL_FORM, {title, isShown, sorting})
      .then(() => getDocuments(LEGAL_FORM))
      .then((response) => sortItemsAndSetItems([...response] as TLegalForm[]))
      .catch((error) => console.log(error))
      .finally(() => setCreateModal(false));
  };

  const onEdit = (title: string, isShown: boolean, sorting: number) => {
    updateDocument(LEGAL_FORM, {...currentItem, title, isShown, sorting})
      .then(() => getDocuments(LEGAL_FORM))
      .then((response) => sortItemsAndSetItems([...response] as TLegalForm[]))
      .catch((error) => console.log(error))
      .finally(() => {setEditModal(false), setCurrentItem(null)});
  };

  const onDelete = () => {
    deleteDocument(LEGAL_FORM, currentItem)
      .then(() => getDocuments(LEGAL_FORM))
      .then((response) => sortItemsAndSetItems([...response] as TLegalForm[]))
      .catch((error) => console.log(error))
      .finally(() => {setDeleteModal(false), setCurrentItem(null)})
  };

  useEffect(() => {
    getDocuments(LEGAL_FORM)
    .then((response) => sortItemsAndSetItems([...response] as TLegalForm[]))
    .catch((error) => console.log(error))
  }, []);

  // Таблица
  const TableOfItems = () => (
    <TableContainer>
      <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
        <TableHead>
          <TableRow>
            <TableCell>Правовая Форма</TableCell>
            <TableCell align="center">Сортировка</TableCell>
            <TableCell align="center">
              Отображать в публичной части
            </TableCell>
            <TableCell align="right">Действия</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {items.map((item) => (
            <TableRow>
              <TableCell>{item.title}</TableCell>
              <TableCell align="center">{item.sorting}</TableCell>
              <TableCell align="center">{item.isShown && 'Да'}</TableCell>
              <TableCell align="right">
                <ListItemControls
                  onDelete={() => {setCurrentItem(item), setDeleteModal(true)}}
                  onEdit={() => {setCurrentItem(item), setEditModal(true)}}
                  style={{ justifyContent: "flex-end" }}
                />
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )

  return (
      <Box my={4}>
        <Stack
          justifyContent="space-between"
          direction="row"
          alignItems="center"
          mb={2}
          px={1}
        >
          <Typography variant="h4">Правовая Форма</Typography>
          <Button variant="contained" disableElevation onClick={() => setCreateModal(true)}>
            Создать Правовую Форму
          </Button>
        </Stack>

        <CreateOrEditModal 
          isModal={isCreateModal} 
          onClose={() => setCreateModal(false)}
          item={{title: '', isShown: true, sorting: 10}}
          onSave={onCreate}
        />

        <CreateOrEditModal 
          isModal={isEditModal} 
          onClose={() => setEditModal(false)}
          item={currentItem}
          onSave={onEdit}
        />

        <DeleteModal open={isDeleteModal} onClose={() => {setDeleteModal(false), setCurrentItem(null)}} onDelete={onDelete} item={currentItem?.id}>
          Вы собираетесь удалить Правовую Форму "{currentItem?.title}" ?
        </DeleteModal>

        <Box my={2}>
          <Alert severity="info">
            Изменения в этой таблице могут повлиять на уже заполненные 
            прайсы!
          </Alert>
        </Box>

        <Divider />

        {items.length > 0 ? <TableOfItems /> : <EmptyTable>Не создано ни одного элемента.</EmptyTable>}
      </Box>
  );
};
