import React, { useEffect, useState } from 'react';
import './Tariff.scss';
import { Box, Button, Stack, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from '@mui/material';
import { LEGAL_FORM, TLegalForm } from '../Settings/LegalForm/LegalForm';
import { TAXATION_SYSTEM, TTaxationSystem } from '../Settings/TaxationSystem/TaxationSystem';
import { NUMBER_OF_OPERATIONS, NumberOfOperations, TNumberOfOperations } from '../Settings/NumberOfOperations/NumberOfOperations';
import { TARIFF_PLAN, TTariffPlan } from '../Settings/TariffPlan/TariffPlan';
import { getDocuments } from '../../../core/api';
import { ListItemControls } from "../../shared/ListItemControls/ListItemControls";

