interface TTariff {
    tariffPlanId: string;
    legalFormId: string;
    data: {
        id?: string;
        tariffPlanId: string;
        legalFormId: string;
        numberOfOperationsId: string;
        data_1: {
            taxationSystemId: string;
            value: number;
        }[]
    }[];
}

export const Tariff = () => {
    const [legalFormItems, setLegalFormItems] = useState<TLegalForm[]>([]);
    const [taxationSystemItems, setTaxationSystemItems] = useState<TTaxationSystem[]>([]);
    const [numberOfOperationsItems, setNumberOfOperationsItems] = useState<TNumberOfOperations[]>([]);
    const [tariffPlanItems, setTariffPlanItems] = useState<TTariffPlan[]>([]);
    const [tariff, setTariff] = useState<TTariff[]>([]);

    function getTitle<T extends TLegalForm | TTaxationSystem | TNumberOfOperations | TTariffPlan>(arr: T[], id: string): string {
        const obj = arr.find((item) => item.id === id)

        if(obj) {
            return obj.title;
        }

        return null;
    }

    useEffect(() => {
        const data = [
            {collection: LEGAL_FORM, arr: legalFormItems, cb: setLegalFormItems},
            {collection: TAXATION_SYSTEM, arr: taxationSystemItems, cb: setTaxationSystemItems},
            {collection: NUMBER_OF_OPERATIONS, arr: numberOfOperationsItems, cb: setNumberOfOperationsItems},
            {collection: TARIFF_PLAN, arr: tariffPlanItems, cb: setTariffPlanItems}
        ];

        function isLegalForm(form: TLegalForm[] | TTaxationSystem[] | TNumberOfOperations[] | TTariffPlan[]): form is TLegalForm[] {
            return (form[0] as TLegalForm).isShown !== undefined;
        }

        function isTaxationSystem(system: TLegalForm[] | TTaxationSystem[] | TNumberOfOperations[] | TTariffPlan[]): system is TTaxationSystem[] {
            return (system[0] as TTaxationSystem).isShown !== undefined;
        }

        Promise.all(
            data.map(async ({collection}) => {
                const document = await getDocuments(collection);

                return document;
            })
        )
        .then((response) => {
            data.forEach(({arr, cb}, index) => {
                const d = response[index] as typeof arr;
                // if(isLegalForm(d) || isTaxationSystem(d)) {
                //     const a = d.filter((item) => item.isShown).sort((a,b) => a.sorting - b.sorting);
                //     cb(a);
                // } else {
                //     const a = d.sort((a,b) => a.sorting - b.sorting);
                //     cb(a as typeof arr);
                // }
                const a = d.sort((a,b) => a.sorting - b.sorting);
                cb(a as typeof arr);
            })
        })
        .catch((error) => console.log(error))
    }, []);

    useEffect(() => {
        if(legalFormItems.length > 0 && taxationSystemItems.length > 0 && numberOfOperationsItems.length > 0 && tariffPlanItems.length > 0) {
            const data: TTariff[] = [];

            legalFormItems.forEach((legalFormItem) => {
                tariffPlanItems.forEach((tariffPlanItem) => {
                    data.push({
                        tariffPlanId: tariffPlanItem.id,
                        legalFormId: legalFormItem.id,
                        data: numberOfOperationsItems.map((numberOfOperationsItem) => ({
                            tariffPlanId: tariffPlanItem.id,
                            legalFormId: legalFormItem.id,
                            numberOfOperationsId: numberOfOperationsItem.id,
                            data_1: taxationSystemItems.map((taxationSystemItem) => ({
                                taxationSystemId: taxationSystemItem.id,
                                value: 0,
                            }))
                        }))
                    })
                })
            })
        
            // legalFormItems.forEach((tariffItem) => {
            //     data.push({
            //         ...tariffItem, 
            //         data: tariffPlanItems.map((tariffPlanItem) => ({
            //             ...tariffPlanItem,
            //             data: numberOfOperationsItems.map((numberOfOperationsItem) => ({
            //                 ...numberOfOperationsItem,
            //                 data: taxationSystemItems.map((system) => ({
            //                     ...system,
            //                     tariff: 0,
            //                 }))
            //             }))
            //         }))
            //     })
            // });

            setTariff(data);
        }
    }, [legalFormItems, taxationSystemItems, numberOfOperationsItems, tariffPlanItems])

console.log(tariff)
    const TableOfItems = () => (
        <TableContainer>
          <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
            <TableHead>
              <TableRow>
                <TableCell>Количество операций</TableCell>
                {taxationSystemItems.map((item) => (
                    <TableCell>{item.shortTitle ? item.shortTitle : item.title}</TableCell>
                ))}
                <TableCell align="right">Действия</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
                {tariff.map(({tariffPlanId, legalFormId, data}) => (
                    <>
                        <TableRow>
                            <TableCell colSpan={taxationSystemItems.length + 2}>
                                <Typography variant='h6'>
                                    {getTitle(legalFormItems, legalFormId) + " - " + getTitle(tariffPlanItems, tariffPlanId)}
                                </Typography>
                            </TableCell>
                        </TableRow>
                        {data.map(({ numberOfOperationsId, data_1 }) => (
                            <TableRow>
                                <TableCell>{getTitle(numberOfOperationsItems, numberOfOperationsId)}</TableCell>
                                {data_1.map(({ taxationSystemId, value }) => (
                                    <TableCell align='center'>{value}</TableCell>
                                ))}
                                <TableCell align="right">
                                <ListItemControls
                                    onEdit={() => {}}
                                    style={{ justifyContent: "flex-end" }}
                                    />
                                </TableCell>
                            </TableRow>
                        ))}
                    </>
                    
                ))}
            </TableBody>
          </Table>
        </TableContainer>
      )
    
    return (
        <Box my={5}>
            <Stack
            justifyContent="space-between"
            direction="row"
            alignItems="center"
            mb={2}
            px={1}
            >
                <Typography variant="h3">Тарифы</Typography>
                {/* <Button variant="contained" disableElevation onClick={() => {}}>Создать Новость</Button> */}
            </Stack>

            <TableOfItems />
        </Box>
    )
};