import React from "react";
import { Routes, Route, Navigate } from "react-router-dom";
import AdminLayout from "../admin/AdminLayout/AdminLayout";
import LoginPage from "../admin/LoginPage/LoginPage";
import {News as AdminNews} from "../admin/News/News";
import {Price as AdminPrice} from "../admin/Price/Price";
import CreatePrice from "../admin/CreatePrice/CreatePrice";
import { CreateNews }from "../admin/CreateNews/CreateNews";
import PrivateRoute from "../admin/PrivateRoute/PrivateRoute";
import { AuthProvider } from "../admin/PrivateRoute/AuthProvider";
import { Home } from "../admin/Home/Home";
import { Settings } from "../admin/Settings/Settings";
import { initializeAPI } from "../../core/api";
import { Tariff } from "../admin/Tariff/Tariff";

const firebaseApp = initializeAPI();

const Admin = () => {
  return (
    <AuthProvider firebaseApp={firebaseApp}>
      <Routes>
        <Route element={<Navigate to="/admin/home" replace />} index />
        <Route path="login" element={<LoginPage />} />
        <Route
          element={
            <PrivateRoute>
              <AdminLayout />
            </PrivateRoute>
          }
        >
          <Route
            path="home"
            element={
              <PrivateRoute>
                <Home />
              </PrivateRoute>
            }
          />
          <Route path="news">
            <Route
              element={
                <PrivateRoute>
                  <AdminNews />
                </PrivateRoute>
              }
              index
            />
            <Route
              path="create"
              element={
                <PrivateRoute>
                  <CreateNews />
                </PrivateRoute>
              }
            />
            <Route
              path="edit/:id"
              element={
                <PrivateRoute>
                  <CreateNews />
                </PrivateRoute>
              }
            />
          </Route>
          <Route path="price">
            <Route
              element={
                <PrivateRoute>
                  <AdminPrice />
                </PrivateRoute>
              }
              index
            />
            <Route
              path="create"
              element={
                <PrivateRoute>
                  <CreatePrice />
                </PrivateRoute>
              }
            />
            <Route
              path="edit/:id"
              element={
                <PrivateRoute>
                  <CreatePrice />
                </PrivateRoute>
              }
            />
          </Route>
          <Route
            path="settings"
            element={
              <PrivateRoute>
                <Settings />
              </PrivateRoute>
            }
          />
          <Route
            path="tariff"
            element={
              <PrivateRoute>
                <Tariff />
              </PrivateRoute>
            }
          />
        </Route>
      </Routes>
    </AuthProvider>
  );
};

export default Admin;
