import React, { useEffect, useState } from "react";
import "./CreateNews.scss";
import {
  Box,
  Stack,
  Button,
  Divider,
  IconButton,
  TextField,
  Typography,
} from "@mui/material";
import { Link } from "@mui/icons-material";
import { Editor } from "../../shared/Editor/Editor";
import CyrillicToTranslit from 'cyrillic-to-translit-js';
import { today } from "../../../core/utils";
import { useNavigate, useParams } from "react-router-dom";
import { NEWS, TNews } from "../News/News";
import { createDocument, getDocument, updateDocument } from "../../../core/api";

export const CreateNews = () => {
  // @ts-ignore
  const toTranslit = new CyrillicToTranslit();
  const navigate = useNavigate();
  const {id} = useParams();
  const [title, setTitle] = useState('');
  const [link, setLink] = useState('');
  const [date, setDate] = useState(today());
  const [announce, setAnnounce] = useState('');
  const [editorValue, setEditorValue] = useState('');
  const [titleError, setTitleError] = useState(false);
  const [linkError, setLinkError] = useState(false);
  const [dateError, setDateError] = useState(false);
  const [announceError, setAnnounceError] = useState(false);
  const [editorValueError, setEditorValueError] = useState(false);
  const onTranslit = (a: string) => toTranslit.transform(a, '-').toLowerCase();

  useEffect(() => {
    if(id) {
      getDocument(NEWS, id)
        .then((response) => {
          setTitle((response  as TNews).title);
          setLink((response  as TNews).link);
          setDate((response  as TNews).date);
          setAnnounce((response  as TNews).announce);
          setEditorValue((response  as TNews).editorValue);
        })
    }
  }, [])

  const handleSave = () => {
    if(
      title.length > 0 && 
      link.length > 0 && 
      date.length > 0 && date.match(/\d/) &&
      announce.length > 0 && 
      editorValue.length > 0
    ) {
      if(id) {
        updateDocument(NEWS, {id, title, link, date, announce, editorValue})
          .then(() => navigate(-1))
          .catch((error) => console.log(error));
      } else {
        createDocument(NEWS, {title, link, date, announce, editorValue})
          .then(() => navigate(-1))
          .catch((error) => console.log(error))
          .finally(() => {setTitle(''), setLink(''), setDate(today()), setAnnounce(''), setEditorValue('')});
      }
      setTitleError(false);
      setLinkError(false);
      setDateError(false);
      setAnnounceError(false);
      setEditorValueError(false);
    } else {
      title.length === 0 ? setTitleError(true) : setTitleError(false);
      link.length === 0 ? setLinkError(true) : setLinkError(false);
      date.length === 0 ? setDateError(true) : setDateError(false);
      announce.length === 0 ? setAnnounceError(true) : setAnnounceError(false);
      editorValue.length === 0 ? setEditorValueError(true) : setEditorValueError(false);
    }
  }
console.log(title, link, date, announce, editorValue)
  return (
    <Box my={5}>
      <Stack
        justifyContent="space-between"
        direction="row"
        alignItems="center"
        mb={2}
        px={1}
      >
        <Typography variant="h3">Создать Новость</Typography>
      </Stack>

      <Divider />

      <TextField
        id="outlined-basic"
        label="Название"
        variant="outlined"
        required
        fullWidth
        margin="normal"
        size="small"
        value={title}
        onChange={(e) => setTitle(e.target.value)}
        error={titleError}
      />
      <IconButton edge="end" onClick={() => setLink(onTranslit(title))}>
        <Link />
      </IconButton>
      <TextField
        id="outlined-basic"
        label="Символьный код"
        variant="outlined"
        required
        fullWidth
        margin="dense"
        size="small"
        value={link}
        onChange={(e) => setLink(e.target.value)}
        error={linkError}
      />
      <TextField
        id="outlined-basic"
        variant="outlined"
        required
        fullWidth
        margin="normal"
        size="small"
        type="date"
        value={date}
        onChange={(e) => setDate(e.target.value)}
        error={dateError}
      />
      <TextField
        id="outlined-basic"
        label="Анонс"
        variant="outlined"
        required
        fullWidth
        margin="normal"
        size="small"
        multiline
        rows={2}
        value={announce}
        onChange={(e) => setAnnounce(e.target.value)}
        error={announceError}
      />
      <Editor placeholder="Тело новости" editorValue={editorValue} setEditorValue={setEditorValue} error={editorValueError} />
      <Stack
        justifyContent="flex-start"
        direction="row"
        alignItems="center"
        mb={4}
        spacing={2}
      >
        <Button variant="contained" disableElevation onClick={handleSave}>
          Сохранить
        </Button>
        <Button variant="outlined" onClick={() => navigate(-1)}>Отменить</Button>
      </Stack>
    </Box>
  );
};
