import React, { FC, HTMLAttributes, useState, useEffect } from 'react';
import { Dialog, DialogTitle, DialogContent, DialogContentText, TextField, FormControlLabel, Stack, Switch, DialogActions, Button } from '@mui/material';
import { TNumberOfOperations } from './NumberOfOperations';

interface CreateOrEditModalProps extends HTMLAttributes<HTMLDivElement> {
    onSave: (title: string, sorting: number, min: number, max: number) => void;
    item: TNumberOfOperations | Omit<TNumberOfOperations, 'id'>;
    isModal: boolean;
    onClose: () => void;
}

export const CreateOrEditModal: FC<CreateOrEditModalProps> = ({ onSave, isModal, item, onClose }) => {
    // TODO: Переделать на useReducer
    const [title, setTitle] = useState('');
    const [minValue, setMinValue] = useState(0);
    const [maxValue, setMaxValue] = useState(0);
    const [sorting, setSorting] = useState(10);
    const [titleError, setTitleError] = useState(false);
    const [sortingError, setSortingError] = useState(false);
    const [minValueError, setMinValueError] = useState(false);
    const [maxValueError, setMaxValueError] = useState(false);

    useEffect(() => {
        if(item) {
            setTitle(item.title)
            setMinValue(item.min)
            setMaxValue(item.max)
            setSorting(item.sorting)
        }
    }, [item])

    const handleSave = () => {
        if(title.length > 0  && sorting > 0 && minValue > 0 && maxValue > 0) {
            onSave(title, minValue, maxValue, sorting)
            setTitleError(false)
            setSortingError(false)
            setMinValueError(false)
            setMaxValueError(false)
        } else {
            title.length === 0 ? setTitleError(true) : setTitleError(false);
            sorting < 1 ? setSortingError(true) : setSortingError(false);
            minValue < 1 ? setMinValueError(true) : setMinValueError(false);
            maxValue < 1 ?  setMaxValueError(true) : setMaxValueError(false);
        }
    }

    return (
        <Dialog open={isModal} onClose={onClose}>
            <DialogTitle>{item?.hasOwnProperty('id') ? 'Редактировать' : 'Создать'} Элемент</DialogTitle>
            <DialogContent  sx={{minWidth: '400px'}}>
                <DialogContentText>
                    Введите Количество Операций:
                </DialogContentText>
                <TextField 
                    label="Количество Операций"
                    variant="outlined"
                    required
                    fullWidth
                    margin="normal"
                    size="small"
                    type="text"
                    value={title}
                    onChange={(e) => setTitle(e.target.value)}
                    error={titleError}
                ></TextField>
                <Stack 
                    justifyContent="center"
                    direction="row"
                    alignItems="center"
                    spacing={2}
                >
                    <TextField 
                        label="Минимальное значение"
                        variant="outlined"
                        required
                        fullWidth
                        size="small"
                        type="text"
                        sx={{margin: '16px 0'}}
                        value={minValue}
                        onChange={(e) => setMinValue(+e.target.value)}
                        error={minValueError}
                    ></TextField>
                    <TextField 
                        label="Максимальное значение"
                        variant="outlined"
                        required
                        fullWidth
                        size="small"
                        type="text"
                        sx={{margin: '16px 0'}}
                        value={maxValue}
                        onChange={(e) => setMaxValue(+e.target.value)}
                        error={maxValueError}
                    ></TextField>
                </Stack>
                <TextField 
                    label="Сортировка"
                    variant="outlined"
                    required
                    fullWidth
                    margin="normal"
                    size="small"
                    type="number"
                    value={sorting}
                    onChange={(e) => setSorting(+e.target.value)}
                    error={sortingError}
                ></TextField>
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose}>Отмена</Button>
                <Button onClick={handleSave}>Сохранить</Button>
            </DialogActions>
        </Dialog>
    )
}

