import React, { FC, createContext, useContext, useEffect, useState } from "react";
import { FirebaseApp } from "firebase/app";
import { 
  getAuth, 
  signInWithEmailAndPassword, 
  User, 
  UserCredential, 
  signOut, 
  browserLocalPersistence, 
  browserSessionPersistence,
  inMemoryPersistence, 
} from "firebase/auth";
import { getFirestore, doc, getDoc } from "firebase/firestore";

type TProps = {
  children: React.ReactNode;
  firebaseApp: FirebaseApp;
};

export type TLoginWithEmailAndPasswordResult = UserCredential;

export type TAuthContext = {
  isAuthenticated: boolean | null; // boolean - определенное состояние. null - неизвестное (загрузка)
  user?: any;
  logIn: ((email: string, password: string, rememberMe: boolean) => Promise<TLoginWithEmailAndPasswordResult>) | (() => Promise<never>);
  logOut: () => void;
};

export const AuthContext = createContext({
  isAuthenticated: null,
  logIn: () => Promise.reject({}),
  logOut: () => void 0,
});

export const useAuth = (): TAuthContext => useContext<TAuthContext>(AuthContext);

const isUserAdmin = async (firebaseApp: FirebaseApp) => {
  const db = getFirestore(firebaseApp);
  return await getDoc(doc(db, '/internal/auth'));
};

export const AuthProvider: FC<TProps> = ({ firebaseApp, children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [isAuthenticated, setAuthenticated] = useState<TAuthContext['isAuthenticated']>(null);
  const [auth] = useState(getAuth(firebaseApp));

  useEffect(() => {
    auth.onAuthStateChanged((user) => {
      if(user) {
        isUserAdmin(firebaseApp)
          .then(() => {
            setAuthenticated(true)
            setUser(user)
          })
          .catch((e) => {
            console.error(e);
            setAuthenticated(false);
            setUser(null);
            signOut(auth);
          })
      } else {
        setAuthenticated(false)
        setUser(null)
      }
    })
  }, [])

  const logIn = (email: string, password: string, rememberMe: boolean) => {

    const processLogin = (loginPromise: Promise<void>) => {

      return loginPromise
        .then(() => {setUser(null), setAuthenticated(null)})
        .then(() => signInWithEmailAndPassword(auth, email, password))
        .then((result) => result)
        .catch((error) => {throw error});
    };

    return rememberMe 
      ? processLogin(auth.setPersistence(browserLocalPersistence)) 
      : processLogin(auth.setPersistence(inMemoryPersistence))
  }

  const logOut = () => signOut(auth);

console.log(user)
  return (
    <AuthContext.Provider
      value={{ 
        isAuthenticated, 
        user, 
        // @ts-ignore
        logIn, 
        logOut,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};
