export const createDocument = async <T extends object>(collectionName: string, data: T): Promise<T&{id: string}> => {
    const db = getFirestore();
    const ref = collection(db, collectionName);

    try {
        const response = await addDoc(ref, data);

        return {...data, id: response.id};
    } catch(error) {
        return Promise.reject(error)
    }
}

export const updateDocument = async <T extends object&{id:string}>(collectionName: string, data: T): Promise<T> => {
    const db = getFirestore();
    const ref = doc(db, collectionName, data.id);

    try {
        await updateDoc(ref, data);

        return data;
    } catch(error) {
        return Promise.reject(error)
    }
}

export const deleteDocument = async <T extends object&{id:string}>(collectionName: string, data: T): Promise<T> => {
    const db = getFirestore();
    const ref = doc(db, collectionName, data.id);

    try {
        await deleteDoc(ref);

        return data;
    } catch(error) {
        return Promise.reject(error)
    }
}

export const getDocuments = async <T>(collectionName: string): Promise<T[]> => {
    const db = getFirestore();
    const documents: T[] = [];
  
    try {
      const querySnapshot = await getDocs(collection(db, collectionName));
  
      querySnapshot.forEach((doc) => {
        const data = doc.data() as Omit<T, 'id'>;
  
        documents.push({...data, id: doc.id} as T);
      });
    } catch (error) {
      return Promise.reject(error);
    }
  
    return documents;
  };

  export const getDocument = async <T>(collectionName: string, id: string): Promise<T> => {
    const db = getFirestore();
    const ref = doc(db, collectionName, id);
    
    try {
        const docSnap = await getDoc(ref);
        const response = docSnap.data();

        return response as T;
    } catch(e) {
        return Promise.reject(e)
    }
  }