import React, { FC, HTMLAttributes } from "react";
import "./Editor.scss";
import { Box } from "@mui/material";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";

interface EditorProps extends HTMLAttributes<HTMLDivElement> {
    placeholder: string;
    editorValue: string;
    setEditorValue: (editorValue: string) => void;
    error: boolean;
}

export const Editor: FC<EditorProps> = ({ editorValue, error, placeholder, setEditorValue }) => {

  const modules = {
    toolbar: [
      [{ header: [1, 2, false] }],
      ["bold", "italic", "underline"],
      [{ list: "bullet" }],
      [{ align: [] as unknown as string }],
      [
        { color: [] as unknown as string },
        { background: [] as unknown as string },
      ],
      ["link"],
      ["clean"],
    ],
  };

  console.log(editorValue);
  return (
    <Box my={2} sx={{ borderRadius: "4px", border: error ? "1px solid red" : "1px solid #eee" }}>
        <ReactQuill
          theme="snow"
          value={editorValue}
          onChange={setEditorValue}
          placeholder={placeholder}
          modules={modules}
          style={{ height: "100%" }}
        />
    </Box>
  );
};
